import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoForCli } from '../../../../models/dtos/ger/grupoForCli';

interface GrupoForCliEditViewProps extends EditViewProps<GrupoForCli> {}

const GrupoForCliEditView: React.FC<GrupoForCliEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<GrupoForCli>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Grupo de Fornecedor/Cliente/Transportadora/Operadora de Cartão">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 4) } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              placeholder="Informe o código"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};
export default GrupoForCliEditView;
